#home {
     display: flex;
     justify-content: space-evenly;
     padding-top: 70px;
}

.home_heading {
     font-size: 50px;
}

.home-img {
     width: 80%;
     max-width: 400px;
     margin: 0 auto;
}

.resume_download {
     display: flex;
     margin-top: 2em;
     text-align: center;
}

.resume_btn {
     width: 8vw;
     height: 6vh;
     background-color: rgb(188, 183, 183);
     cursor: pointer;
}

.resume_btn span {
     margin-right: 0.5em;
}

@media screen and (max-width: 768px) {
     .home_heading {
          font-size: 36px;
     }

     .home-img {
          width: 100%;
          max-width: none;
     }

     .resume_btn {
          width: 100%;
          font-size: 14px;
          padding: 0.5em 1em;
     }
}