#projects {
    text-align: center;
    padding-top: 70px;
}

.project-container {
    width: 75%;
    margin: 3% auto;
}

.project-container:hover .hover-buttons {
    visibility: visible;
}

.project-card {
    display: flex;
    align-items: center;
    margin: 2% auto;
    position: relative;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.project-tech-stack{
    display: flex;
    align-items: center;
    font-size: 14px;
}

.project-tech-stack>h5{
    color: red;
    font-size: 14px;
}

.project-tech-stack>div{
    display: flex;
    justify-content: space-evenly;
    gap: 5px;
    margin-left:2%
}

.hover-buttons {
    display: flex;
    justify-content: space-evenly;
    visibility: hidden;
    width: 17%;
    margin: auto;
    background: rgb(188, 183, 183);
    position: absolute;
    top: calc(90%);
    left: 0;
    right: 0;
}

.hover-buttons a {
    text-decoration: none;
}


.hover-buttons i {
    font-size: x-large;
}

.project-card>img {
    width: 55%;
}

.content-container {
    display: flex;
    flex-direction: column;
    text-align: initial;
    padding: 0px 2vw;
}

/* For screens larger than 768px */
@media only screen and (max-width: 600px) {
    .project-description{
        display: none;
    }
}
@media screen and (max-width: 768px) {
    .project-container {
        width: 75%;
    }

    .project-card>img {
        width: 55%;
    }
}

@media screen and (min-width: 1024px) {
    .project-container {
        width: 60%;
    }

    .project-card>img {
        width: 50%;
    }
}