.navbar {
  display: flex;
  align-items: center;
  top: 0;
  position: sticky;
  z-index:20;
  justify-content: space-between;
  background-color: rgb(241, 237, 233);
}

.nav-logo {
  margin-left: 5vw;
}

nav{
  display: flex;
  width: 60%;
  justify-content: space-evenly;
  margin-right: 1vw;
}

nav a {
  text-decoration: none;
  color: black;
  margin: 10px 1vw;
  font-size: 20px;
}

.nav-close a {
  text-decoration: none;
  color: black;
  margin: 10px 0;
  font-size: 20px;
}

.changer {
  display: none;
}

.menu-button {
  display: block;
  padding: 10px;
  margin-right: 20px;
  visibility: hidden;
}

.menu {
  font-size: 30px;
}

.nav-open {
  display: flex;
}

@media (max-width: 768px) {
  .nav-logo {
    margin-left: 3vwpx;
  }

  .nav-close{
    display: none;
  }
  
  nav {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 40px;
  }

  nav>a {
    margin: 10px 0;
    font-size: 24px;
  }

  .changer {
    display: none;
  }

  .menu-button {
    visibility: visible;
  }

  .menu {
    font-size: 36px;
  }
}