#about{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 70px;
}

.github_gif_man{
    width: 40%;
}

.about_p{
    width: 70%;
}

/* For screens smaller than 600px */
@media only screen and (max-width: 600px) {
    #about {
      margin: 10vw auto;
    }
    
    .github_gif_man {
      width: 70%;
    }
    
    .about_p {
      width: 90%;
      font-size: 14px;
    }
  }
  
  /* For screens between 600px and 900px */
  @media only screen and (min-width: 600px) and (max-width: 900px) {
    #about {
      margin: 5vw auto;
    }
    
    .github_gif_man {
      width: 60%;
    }
    
    .about_p {
      width: 80%;
      font-size: 16px;
    }
  }
  
  /* For screens larger than 900px */
  @media only screen and (min-width: 900px) {
    #about {
      margin: 2vw auto;
    }
    
    .github_gif_man {
      width: 40%;
    }
    
    .about_p {
      width: 70%;
      font-size: 18px;
    }
  }
  

